        information: `આ શાખાના અંતર્ગત દરેક હિન્દુ ખેડૂતોને ખેતી વિશેનોપુરુ માર્ગદર્શન સરકારો થી આવનારી સહાય લાભો થી અવગત કરાવવા તથા યોજનાબદ્ધ રીતે ખેતી પાક વાવેતર ઓર્ગેનિક ખેતી ગૌ આધારિત ખેતી અનેક પ્રકારની ખેતી વિશે જાણકારી તથા માહિતી આપવી અને આપણાં દરેક હિન્દુ ખેડૂતોને રોજગારી મળી રહે તેના માટેના પ્રયાસો કરવા તથા જમીન માફક આવે તેવી જગ્યાએ ઔષધી ખેતી અને જેની હાલ બજારમાં છે એવા પાકો ની ખેતી તથા એના વિશેની સમજુતી માહિતી પૂરી પાડવી અને ખેડૂતને પૂરતું વળતર અને ધિરાણ મળી રહે તેના માટે પ્રયત્ન કરવા આ બધું જ ખેડૂત પુત્ર શાખા ના અંતર્ગત કરવામાં આવશે સાથે સાથે ખેડૂતોને મળતી સબસીડી જેમ કે વાહનો ઓજારોઆવાસો આ દરેક માટે માધ્યમ બની અને મદદ કરવી અને ખેડૂતોને યુરિયા ખાતર તથા વિદેશી દવાઓનાયંત્રોથી મુક્ત કરાવી પોતાના જમીન નું પોષણ તથા કશ જળવાઈ  અને ઉપજાઉ બની રહે એ માટે સાચી દિશા અને માહિતી આપવી`,
